@use '../sass/mixins.module.scss';

.health-bar {
    position: fixed;
    bottom: 0;
    left: 0;
    border: black 2px solid;
    height: 95vh;
    width: 5vw;
    span{
        position: absolute;
        bottom: 0;
        left: 0;
        height: 100%;
        width: 4vw;
        background: blue;;
    }

}